.header {
  display: flex;
  width: 1097px;
  min-height: 119px;
  justify-content: space-between;
  align-items: center;
  padding: 20px 39px 21px 30px;
  box-sizing: border-box;

  &__logo {
    margin: 10px -47px 10px 10px;
    width: 186px;
    height: 58px;
    object-fit: cover;
  }

  &__navbar {
    margin: 0;
    margin-bottom: 3px;
    padding: 0;
    list-style-type: none;
    display: flex;
    gap: 32px;
  }

  &__navbar {
    margin: 0;
    margin-bottom: 3px;
    padding: 0;
    list-style-type: none;
    display: flex;
    gap: 32px;
  }

  &__link {
    padding: 10px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: 0.2px;
  }

  &__button {
    margin: 0;
    padding: 0;
    margin-bottom: 3px;
    width: 139px;
    height: 52px;
    box-sizing: border-box;
    border: 1px solid var(--light-text, #FFF);
    box-shadow: 0px 4px 31px 0px rgba(0, 0, 0, 0.15);

    &__text {
      margin: 0;
      padding: 0;
      padding-right: 1px;
      font-size: 17px;
      font-weight: 500;
      line-height: 25px;
      letter-spacing: 0.2px;
    }
  }

  @media screen and (max-width: 1097px) {
    width: 100%;

    &__logo{
      margin-right: 0;
    }
  }
}